@import "./_base";
@import "./_variable";

@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Noto+Sans+TC:wght@400;700&family=Roboto:ital,wght@1,500&display=swap');

@include keyframes(swing) {
  0% {
    transform: rotate(5deg); }
  100% {
    transform: rotate(-5deg); } }

* {
  padding: 0;
  margin: 0;
  font-family: 'Noto Sans TC', sans-serif;
  color: $gray01; }

/* Chrome, Safari, Edge, Opera */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; }

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield; }

img {
  width: 100%;
  vertical-align: middle; }

header {
  width: 100%;
  padding: 15px 30px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  figure.logo {
    width: 145px; }
  .box {
    display: flex;
    align-items: center;
    p.txt {
      padding: 0 10px;
      font-size: 1rem;
      color: $gray05;
      cursor: pointer;
      @include dib;
      &:nth-child(3) {
        margin-right: 80px; } }
    iframe {
      margin: 0 10px; }
    a.share {
      width: 80px;
      margin: 0 10px;
      cursor: pointer;
      @include dib; } }
  .mobile-menu {
    display: none; }
  @include rwd(1000px) {
    // padding: 15px 30px
    // display: flex
    figure.logo {
 }      // width: 145px
    .box {
      p.txt {
        padding: 0 5px;
        font-size: 0.8rem;
        &:nth-child(3) {
          margin-right: 10px; } }
      iframe {
        margin: 0 5px; }
      a.share {
        width: 50px;
        margin: 0 5px; } } }
  @include rwd(768px) {
    padding: 10px 15px;
    .box {
      display: flex;
      align-items: center;
      p.txt {
        display: none; }
      .ham {
        width: 30px;
        height: 20px;
        margin-left: 10px;
        position: relative;
        z-index: 201;
        cursor: pointer;
        .line {
          width: 100%;
          height: 4px;
          background-color: $gray01;
          position: absolute;
          left: 0;
          right: 0;
          margin: auto;
          &:nth-child(1) {
            top: 0; }
          &:nth-child(2) {
            top: 0;
            bottom: 0; }
          &:nth-child(3) {
            bottom: 0; } } } }
    .mobile-menu {
      display: block;
      width: 100vw;
      height: 100vh;
      padding: 2rem;
      box-sizing: border-box;
      position: fixed;
      top: 0;
      left: 100vw;
      background-color: #fff;
      z-index: 200;
      transition: .3s;
      p.txt {
        padding: 1rem;
        font-size: 1.25rem;
        text-align: center;
        cursor: pointer;
        & + p.txt {
          border-top: 1px solid $gray03; } } }
    &.open {
      .box {
        .ham {
          .line {
            &:nth-child(1) {
              top: calc( 50% - 2px );
              transform: rotate(45deg); }
            &:nth-child(2) {
              opacity: 0; }
            &:nth-child(3) {
              bottom: calc( 50% - 2px );
              transform: rotate(-45deg); } } } }
      .mobile-menu {
        left: 0; } } } }

footer.pam-footer {
  border: 1px solid $gray03;
  nav.pam-footer-nav {
    padding: 10px 20px;
    ul.pam-footer-nav__list {
      display: flex;
      list-style: none;
      flex-wrap: wrap;
      justify-content: center;
      li.pam-footer-nav__item {
        padding: 10px 0 10px 10px;
        a {
          text-decoration: none;
          font-size: 0.9rem;
          color: $blue02;
          font-weight: bold;
          @include rwd(768px) {
            font-size: 0.875rem; } }
        span.pam-footer-nav__item-pipe {
          font-size: 0.9rem;
          color: $blue02;
          font-weight: bold;
          padding-left: 10px;
          @include rwd(768px) {
            font-size: 0.875rem; } } } } }
  section.pam-footer-contact {
    padding: 10px 20px;
    display: flex;
    flex-direction: column;
    ul.pam-footer-contact__list {
      display: flex;
      list-style: none;
      flex-wrap: wrap;
      justify-content: center;
      li.pam-footer-contact__item {
        padding-bottom: 10px;
        color: #68737a;
        font-size: 0.9rem;
        text-align: center;
        line-height: 20px;
        @include rwd(768px) {
          font-size: 0.75rem; }
        span.phone {
          color: #222;
          font-size: 0.9rem;
          @include rwd(768px) {
            font-size: 0.875rem; } } }
      li.pam-footer-contact__item {
        color: #68737a;
        font-size: 0.9rem;
        text-align: center;
        line-height: 20px;
        @include rwd(768px) {
          font-size: 0.75rem; } } } }
  section.pam-footer-copywriting {
    background-color: #ed1b2e;
    display: flex;
    justify-content: center;
    color: #fff;
    padding: 10px 0;
    font-size: 0.9rem;
    font-weight: 700;
    line-height: 20px;
    @include rwd(768px) {
      font-size: 0.75rem; } } }


figure.fix-btn {
  width: 200px;
  position: fixed;
  right: 2rem;
  bottom: 6rem;
  z-index: 100;
  cursor: pointer;
  @include rwd(1280px) {
    width: 150px;
    right: 1rem;
    bottom: 3rem; }
  @include rwd(768px) {
    width: 80px;
    right: 0.5rem; }
  @include rwd(540px) {
    width: 60px;
    right: 0.2rem; } }

.success-popup {
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(#fff,.7);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  .main {
    max-width: 590px;
    width: 95%;
    padding: 25px 40px;
    border: 3px solid #000;
    border-radius: 1rem;
    background-color: #fff;
    box-sizing: border-box;
    text-align: center;
    @include pstc5;
    .close {
      width: 60px;
      margin-left: auto;
      cursor: pointer;
      .close-btn {
        width: 60px;
        height: 60px;
        border: 3px solid #000;
        background-color: $red06;
        border-radius: 50%;
        box-sizing: border-box;
        position: relative;
        transform: rotate(45deg);
        &:before,&:after {
          content: '';
          display: block;
          background-color: #000;
          @include pstc5; }
        &:before {
          width: 3px;
          height: 70%; }
        &:after {
          width: 70%;
          height: 3px; } }
      p {
        font-size: 1.2rem;
        color: #000; } }
    .info {
      padding-bottom: 50px;
      font-size: 1.25rem;
      line-height: 1.7; } }
  @include rwd(540px) {
    .main {
      padding: 15px;
      .close {
        width: 40px;
        .close-btn {
          width: 40px;
          height: 40px; }
        p {
          font-size: 0.8rem; } }
      .info {
        padding-bottom: 30px;
        font-size: 0.8rem; } } } }


